export default {
	data() {
		return {
			day : '',
			month : '',	
			news : [],
			list:[],
	    articleList:[],
			hours : ''
		} //return
	},
	onLoad:function(){
		var _thes = this;
		this.getNews();
		this.day = new Date().getDate();
		this.getMonths();
		this.getHourses();
		console.log(_thes.hours);			
	},
	onPullDownRefresh:function(){			
		this.getNews();			
	},
	methods: {  
		getNews : function(){
			var _thas = this;
			console.log(111) ;
			uni.request({		
				url: 'http://news-at.zhihu.com/api/4/news/latest',
				method: 'GET',
				data: {},
				success: res => {
					 console.log(res.data);
					// var list=res.data.split('--hcSplitor');
					// _self.list=list;
					for(var i=0;i<res.data.stories.length;i++){
						_thas.news.push(res.data.stories[i])
					}
					for(var i=0;i<res.data.top_stories.length;i++){
						_thas.list.push(res.data.top_stories[i])
					}
					console.log(_thas.news);
					uni.stopPullDownRefresh();
				},
				fail: () => {},
				complete: () => {}
			});
		},
		tiao(){
			uni.redirectTo({
				url: 'Mine'
			});
		},
		bot(){
			uni.redirectTo({
				url: 'Details'
			});
		},
		getMonths(){
				var _thas = this;
				switch (new Date().getMonth()+1){
					case 1:
					_thas.month = '一月';
					break;
					case 2:
					_thas.month = '二月';
					break;
					case 3:
					_thas.month = '三月';
					break;
					case 4:
					_thas.month = '四月';
					break;
					case 5:
					_thas.month = '五月';
					break;
					case 6:
					_thas.month = '六月';
					break;
					case 7:
					_thas.month = '七月';
					break;
					case 8:
					_thas.month = '八月';
					break;
					case 9:
					_thas.month = '九月';
					break;
					case 10:
					_thas.month = '十月';
					break;
					case 11:
					_thas.month = '十一月';
					break;
					case 12:
					_thas.month = '十二月';
					break;
					default:
						_thas.month = '错误';
						break;
				}		
		},
		getHourses(){
			var _thas =  this;
			var h = new Date().getHours();
			
			if(0<=h&& h<6){
			 return  _thas.hours = '早晨好';
			}
			if(6<=h&& h<11){
			 return _thas.hours = '上午好';
			}
			if(11<=h&& h<13){
			 return _thas.hours = '中午好';
			}
			if(13<=h&& h<17){
			 return _thas.hours = '下午好';
			}
			if(17<=h&& h<24) {
			 return _thas.hours = '晚上好';
			}
	}
}
}
